/**
 * Adds two numbers
 * @param firstNumber consist of the first opernad of the addition
 * @param secondNumber consist of the second opernad of the addition
 * @return The addition of the two numbers
 * '''typescript
 * add(1,7) = 8
 * '''
 */

export function add(firstNumber: number, secondNumber: number) {
    return firstNumber +secondNumber;
}

console.log("add(1,7): " + add(1,7));